.note-trigger{
	background-color: #fff;
	border-bottom: 1px solid #e7e7e7;
  margin-bottom: 10px;
  li{
    font-weight: bold;
    padding: 10px;
    cursor: pointer;

    &:hover {
      color: #a46b6b;
    }
  }
  .li-active {
      border-bottom: 2px solid #683047;
      margin-bottom: -2px;
      color: #a46b6b;
  }
}


